}
@-webkit-keyframes homeanimate{
    0%{
        opacity:0;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        opacity:1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes homeanimate{
    0%{
        opacity:0;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        opacity:1;
